import React from "react";
import Slider from "react-slick-slider";
import vid1 from "../assets/videos/vid4.mp4"
import vid2 from "../assets/videos/vid1.mp4"
import vid3 from "../assets/videos/vid2.mp4"
import vid4 from "../assets/videos/vid3.mp4"

const Work = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    responsive: [
      { breakpoint: 460, settings: { slidesToShow: 1 } },
      { breakpoint: 970, settings: { slidesToShow: 1 } },
      { breakpoint: 1180, settings: { slidesToShow: 1 } },
      { breakpoint: 100000, settings: { slidesToShow: 3 } },
    ],
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 10000
  };

  const getDesc = i => {
    switch (i) {
      case 0: return "Car Race";
      case 1: return "TPP Adventure";
      case 2: return "TPP Shooter";
      case 3: return "RPG";
    }
  }

  return (
    <div className="portfolio-area ptb--120 bg_color--1">
      <div className="portfolio-sacousel-inner mb--55">
        <div className="portfolio-wrapper">
          <div className="container">
            <div className="row">
              <div /* className="col-lg-6" */>
                <div className="section-title">
                  <h2>Game Development</h2>
                  <p>
                    Not professionally experienced, just a passion for mobile game development.
                    Familiar with tools like Unity and Unreal Engine.
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div className="portfolio-slick-activation mt--70 mt_sm--40 show-on-scroll">
            <Slider {...settings}>
              {[vid1, vid2, vid3, vid4].map((item,i) => {
                return (
                    <div key={"slider" + i}>
                      <div
                        className="portfolio"
                        tabIndex="-1"
                        style={{ width: "100%", display: "inline-block" }}
                      >
                        <div className="thumbnail-inner">
                          <video autoPlay muted loop style={{width: '100%'}}>
                            <source src={item} type="video/mp4" />
                          </video>
                        </div>
                        <div className="content">
                          <div className="inner">
                            <p>Game Development</p>
                            <h4 className="title">
                              <a href="https://www.unrealengine.com" target="_blank" rel="noreferrer">
                                {getDesc(i)}
                              </a>
                            </h4>
                            <div className="portfolio-button">
                              <a className="rn-btn" href="https://www.unrealengine.com" target="_blank" rel="noreferrer">
                                Unreal Engine
                              </a>
                            </div>
                          </div>
                        </div>
                        <a className="link-overlay" href="https://www.unrealengine.com" target="_blank" rel="noreferrer">.</a>
                      </div>
                    </div>
                );
              })}
            </Slider>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Work;
